
import { Bell, Settings, User, LogOut } from "lucide-react";
import { Button } from "./ui/button";
import { ThemeToggle } from "./theme-toggle";
import { LogoutLink } from "@kinde-oss/kinde-auth-nextjs/components";
import { Popover, PopoverContent, PopoverTrigger } from "./ui/popover";
import { ProfileAvatar } from "./profile-avatar";
import { Separator } from "./ui/separator";
import { Badge } from "./ui/badge";

interface Props{
    id:string;
    name:string;
    email:string;
    image:string;
}

export const Navbar=({id,name,email,image}:Props)=>{
    return( 
        <nav className="w-full flex items-center justify-between p-6 bg-card/50 backdrop-blur-sm border-b border-border/50">
            <div className="space-y-1">
                <h1 className="text-2xl font-bold bg-gradient-to-r from-foreground to-foreground/70 bg-clip-text text-transparent">
                    Dashboard
                </h1>
                <span className="text-sm text-muted-foreground">
                    Manage all your tasks in one place
                </span>
            </div>
            <div className="flex items-center gap-3">
                <Button variant="ghost" size="icon" className="relative hover:bg-accent/50">
                    <Bell className="h-4 w-4"/>
                    <Badge 
                        variant="destructive" 
                        className="absolute -top-1 -right-1 h-5 w-5 flex items-center justify-center p-0 text-xs"
                    >
                        3
                    </Badge>
                </Button>
                <ThemeToggle/>
                <Popover>
                    <PopoverTrigger asChild>
                        <Button variant="ghost" className="relative p-1 hover:bg-accent/50">
                            <ProfileAvatar url={image || undefined} name={name} className="h-8 w-8"/>
                        </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-64 p-4 shadow-large" align="end">
                        <div className="flex flex-col space-y-3">
                            <div className="flex items-center space-x-3 pb-3">
                                <ProfileAvatar url={image || undefined} name={name} className="h-12 w-12"/>
                                <div className="flex-1 min-w-0">
                                    <h3 className="text-sm font-semibold truncate">{name}</h3>
                                    <p className="text-xs text-muted-foreground truncate">{email}</p>
                                </div>
                            </div>
                            
                            <Separator/>
                            
                            <div className="space-y-1">
                                <Button variant="ghost" className="w-full justify-start text-sm h-9" disabled>
                                    <User className="h-4 w-4 mr-3"/>
                                    Profile Settings
                                </Button>
                                <Button variant="ghost" className="w-full justify-start text-sm h-9" disabled>
                                    <Settings className="h-4 w-4 mr-3"/>
                                    Preferences
                                </Button>
                            </div>
                            
                            <Separator/>
                            
                            <Button asChild variant="ghost" className="w-full justify-start text-sm h-9 text-destructive hover:bg-destructive/10 hover:text-destructive">
                                <LogoutLink className="flex items-center">
                                    <LogOut className="h-4 w-4 mr-3"/>
                                    Sign Out
                                </LogoutLink>
                            </Button>
                        </div>
                    </PopoverContent>
                </Popover>
        </div>
    </nav>
    );
};