import * as React from "react"
import { cva, type VariantProps } from "class-variance-authority"

import { cn } from "@/lib/utils"

const badgeVariants = cva(
  "inline-flex items-center rounded-full border px-2.5 py-0.5 text-xs font-medium smooth-transition focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2",
  {
    variants: {
      variant: {
        default:
          "border-transparent bg-primary text-primary-foreground hover:bg-primary/80 shadow-soft",
        secondary:
          "border-transparent bg-secondary text-secondary-foreground hover:bg-secondary/80",
        destructive:
          "border-transparent bg-destructive text-destructive-foreground hover:bg-destructive/80 shadow-soft",
        outline: "text-foreground border-border hover:bg-accent",
        success: "border-transparent bg-green-600 text-white hover:bg-green-700 shadow-soft",
        warning: "border-transparent bg-yellow-600 text-white hover:bg-yellow-700 shadow-soft",
        info: "border-transparent bg-blue-600 text-white hover:bg-blue-700 shadow-soft",
        
        // Role variants
        OWNER:"bg-gradient-to-r from-pink-500 to-pink-600 text-white shadow-soft hover:from-pink-600 hover:to-pink-700",
        MEMBER:"bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-soft hover:from-blue-600 hover:to-blue-700",
        VIWER:"bg-gradient-to-r from-gray-500 to-gray-600 text-white shadow-soft hover:from-gray-600 hover:to-gray-700",
        
        // Status variants
        TODO:"bg-gradient-to-r from-slate-500 to-slate-600 text-white shadow-soft hover:from-slate-600 hover:to-slate-700",
        IN_PROGRESS:"bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-soft hover:from-blue-600 hover:to-blue-700",
        BACKLOG:"bg-gradient-to-r from-gray-400 to-gray-500 text-white shadow-soft hover:from-gray-500 hover:to-gray-600",
        IN_REVIEW:"bg-gradient-to-r from-purple-500 to-purple-600 text-white shadow-soft hover:from-purple-600 hover:to-purple-700",
        COMPLETED:"bg-gradient-to-r from-green-500 to-green-600 text-white shadow-soft hover:from-green-600 hover:to-green-700",
        BLOCKED:"bg-gradient-to-r from-red-500 to-red-600 text-white shadow-soft hover:from-red-600 hover:to-red-700",
        
        // Priority variants
        CRITICAL:"bg-gradient-to-r from-red-600 to-red-700 text-white shadow-soft hover:from-red-700 hover:to-red-800 animate-pulse",
        HIGH:"bg-gradient-to-r from-orange-500 to-orange-600 text-white shadow-soft hover:from-orange-600 hover:to-orange-700",
        MEDIUM:"bg-gradient-to-r from-yellow-500 to-yellow-600 text-white shadow-soft hover:from-yellow-600 hover:to-yellow-700",
        LOW:"bg-gradient-to-r from-emerald-500 to-emerald-600 text-white shadow-soft hover:from-emerald-600 hover:to-emerald-700",
      },
    },
    defaultVariants: {
      variant: "default",
    },
  }
)

export interface BadgeProps
  extends React.HTMLAttributes<HTMLDivElement>,
    VariantProps<typeof badgeVariants> {}

function Badge({ className, variant, ...props }: BadgeProps) {
  return (
    <div className={cn(badgeVariants({ variant }), className)} {...props} />
  )
}

export { Badge, badgeVariants }
